const passwordMinLength = 8;
const minimumRequiredLength = 1;
// eslint-disable-next-line no-useless-escape, max-len
const emailRegex = /^[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?$/;
// eslint-disable-next-line no-useless-escape
const telephoneRegex = /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{2,7}$/;
const internationalTelephoneRegex = /^(\+[0-9\s]{1,5}\s|\(\+[0-9\s]{1,5}\)\s{0,1})?(\d+\s?)*\d+$/;

const monthsWith31Days = [1, 3, 5, 7, 8, 10, 12];
const monthsWith30Days = [4, 6, 9, 11];

function validateDayInMonth(day, month, year) {
  const dayValue = parseInt(day, 10);
  const monthValue = parseInt(month, 10);
  const yearValue = parseInt(year, 10);
  let isValid = dayValue <= 28;

  if (monthsWith31Days.indexOf(monthValue) !== -1) {
    isValid = dayValue <= 31;
  } else if (monthsWith30Days.indexOf(monthValue) !== -1) {
    isValid = dayValue <= 30;
  } else if (dayValue === 29) {
    isValid = yearValue ? isLeapYear(yearValue) : true;
  }

  return isValid;
}

function isLeapYear(year) {
  return year % 4 === 0;
}

export const validateEmail = (value => emailRegex.test(value));

export const validatePassword = (value => !!(value && value.length >= passwordMinLength));

export const validateTelephone = (value => telephoneRegex.test(value));

export const validateInternationalTelephone = (value => internationalTelephoneRegex.test(value));

export const validateNotEmpty = (value => !!(value && value.length >= minimumRequiredLength));

export const validateDay = ((day, month, year) => day > 0 && validateDayInMonth(day, month, year));

/**
 * Check year value is within a specified range relative to the current year
 *
 * @param  {Number} value             e.g. 2016
 * @param  {Number} yearStartOffset   e.g. -10 --> upto 10 years ago
 * @param  {Number} yearEndOffset     e.g. 10 --> upto 10 years in the future
 *
 * @return {Boolean}
 */
export function validateYear(value, yearStartOffset, yearEndOffset) {
  const currentYear = new Date().getFullYear();

  return value >= currentYear + yearStartOffset &&
         value <= currentYear + yearEndOffset;
}



